@import "plugins";
@import "responsive";
@import "mixin";
@import "colors";
@import "fonts";
@import "form";
@import "button";
@import "checkbox";


.tb-font{
  font-family: "proxima-nova",sans-serif;
}

body{
 @extend .tb-font;
}

label{
  @extend .tb-font;
}

.modal{
  @extend .tb-font;
}

.img-responsive{
  margin: 0 auto;
}

.right{
  float: right;
}

.left{
  float: left;
}

.center{
  margin: 0 auto;
}

.row{
  margin-top: 2rem;
  margin-bottom: 2rem;
}

*:focus {
  outline:0 !important;
}


h4{
  font-weight: 400;
}

.tb-body{
  font-family: "proxima-nova", sans-serif;
  padding-top: 130px;
  overflow-x: hidden;
}

.tb-container{
  @include outer-container;
}

.tag{
  display: inline-block;
  padding: 3px 6px ;
  //margin-bottom: 0;
  margin-right: 15px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.42857143;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  cursor: pointer;
  -webkit-user-select: none;
  margin-bottom: 5px;
}

.tb-tag{
  @extend .tag;
  color: $tb-light-gray;
  background-color: transparent;
  border: 1px solid $tb-light-gray;
}

.tb-tag:hover{
  color: $tb-white;
  background-color: transparent;
  border: 1px solid $tb-white;
}

.tb-tag-inverse{
  @extend .tag;
  color: $tb-black;
  background-color: transparent;
  border: 1px solid $tb-black;
}

.tb-tag-inverse:hover{
  color: $tb-dark-gray !important;
  background-color: transparent ;
  border: 1px solid $tb-dark-gray;
}



:host /deep/ .ui-dialog{
  border-radius: 0;
  font-family: "proxima-nova",sans-serif;
  min-width: 400px;
}

:host /deep/.ui-dialog-content{
  padding-bottom: 0;
}

:host /deep/.ui-resizable-se{
  display: none;
}


:host /deep/.ui-growl-icon-close{
  right: 10px;
}

:host /deep/.ui-growl-message-success{
  font-family: "proxima-nova",sans-serif;
  background-color: $tb-green !important;
  border-radius: 0 !important;
  border: none;
  color: $tb-white;
}

:host /deep/.ui-growl-message-error {
  font-family: "proxima-nova",sans-serif;
  background-color: $tb-red !important;
  border-radius: 0 !important;
  border: none;
  color: $tb-white;
}


:host /deep/.ui-dialog.ui-widget.ui-widget-content.ui-corner-all.ui-shadow {
  position: fixed;
  padding: 0;
  overflow: hidden;
  width: 300px;
}


//table


:host/deep/.ui-datatable{
  font-family: "proxima-nova",sans-serif;
}

//header
:host/deep/.ui-datatable-header{
  background-color: $tb-white-smoke;
  color: $tb-dark-gray;
}

//th
:host/deep/th.ui-state-default.ui-unselectable-text{
  background-color: $tb-dark-blue;
  padding: 10px 5px;
}

//title
:host/deep/.ui-column-title {
  color: $tb-white;
  padding: 10px 15px;
  float: left;
}

:host/deep/.ui-datatable thead th{
  border: none;
}

:host/deep/.ui-datatable tbody td{
  padding: 10px 15px;
  border: none;
  color: $tb-dark-gray;
}

:host/deep/.ui-datatable-odd{
  background-color: $tb-white-smoke;
}

:host/deep/.ui-datatable-even{
  background-color: $tb-white;
}

:host/deep/ td.col-button {
  text-align: center;
}

:host/deep/.table-header{
  text-align: left;
  padding: 10px 15px;
}


:host/deep/th.ui-state-default{
  outline: none !important;
}

:host/deep/.ui-sortable-column-icon{
  color: $tb-light-gray;
  padding-top: 15px;
}
