.btn-primary{
  background-color: $tb-green;
  color: $tb-white;
  border-radius: 0;
  border: 1px solid $tb-green;
}

.btn-primary:hover{
  background-color: $tb-light-green;
  border-color: $tb-light-green;
}


.btn-primary-inverse{
  background-color: $tb-green;
  color: $tb-black;
  border-radius: 0;
  border: 1px solid $tb-green;
}

.btn-primary-inverse:hover{
  background-color: $tb-light-green;
  border-color: $tb-light-green;
}

.btn-secondary{
  background-color: $tb-white;;
  color: $tb-black;
  border: 1px solid $tb-black;
  border-radius: 0;
}

.btn-secondary:hover{
  background-color: $tb-black;
  color: $tb-white;
  border: 1px solid transparent;
}


.btn-danger{
  background-color: $tb-red;;
  color: $tb-white;
  border: 1px solid $tb-red;
  border-radius: 0;
}

.btn-danger:hover{
  background-color: #f61834;
  color: $tb-white;
}



.notpublished{
  color: $tb-red;
}

.underreview{
  color: $tb-yellow;
}

.notapproved{
  color: $tb-red;
}

.approved{
  color: $tb-blue;
}

.published{
  color: $tb-green;
}

.archived{
  color: $tb-gray;
}

