.form-control{
  border-radius: 0;
  font-family: "proxima-nova", sans-serif;
  resize: none;
}

.form-control:focus {
  border-color:#ccc;
  outline: 0;
  -webkit-box-shadow: none;
  box-shadow: none;
}

pre, select{
  border-radius: 0;
  font-family: "proxima-nova", sans-serif;
}


.open>.dropdown-menu{
  border-radius: 0;
}

.modal-content{
  border-radius: 0;
}

.modal-title{
  font-family: "proxima-nova", sans-serif;
  font-size: 14px;
}

//Validation
.ng-dirty.ng-touched.ng-invalid{
  border-color: $tb-red;
}
.text-danger{
  color: $tb-red;
}
